// fonts
$default-fonts: Arial, sans-serif
$mon-r: 'Montserrat', $default-fonts

// colors
$grey-dark: #2D3047
$orange: #FE5F00

/*-------------------------------------------------------------------------*/
/*----------------------------- MAIN STYLES -------------------------------*/
/*-------------------------------------------------------------------------*/
a:hover, a:focus
	text-decoration: none
a
	color: inherit
svg
	width: 100%
	height: 100%
body
	min-width: 320px
	font-family: $mon-r
	font-size: 14px
	color: $grey-dark
	line-height: 1.2
.wrapper
	min-width: 320px
	min-height: 100%
	padding-bottom: 85px
.content
	margin: 0 auto
	max-width: 1460px
.row
	+pie-clearfix
.header
	height: 50px
	border-bottom: 1px solid #DAE9E9
	background-color: #FFF
	.content
		position: relative
	a
		text-decoration: none
.main
	position: relative
	z-index: 1
	padding-top: 20px
	background-color: #fff
.footer
	margin-top: -85px
	height: 85px
	border-top: 1px solid #DAE9E9
/* hide phone blocks */
.is-phone
	display: none
/* load more */
.load-more
	display: none
	margin: 20px 0 40px
	font-size: 32px
	font-weight: 700
	color: #fff
	line-height: 39px
	text-align: center
	&__btn
		display: inline-block
		padding: 10px 20px 11px
		text-decoration: none
		border-radius: 5px
		background-color: $orange
		i
			position: relative
			display: inline-block
			margin-right: 5px
			top: -2px
			left: -7px
			font-size: 24px
			line-height: 24px
/* logo */
.logo
	float: left
	margin-left: 40px
	margin-right: 40px
	a
		display: block
		padding: 7px 0
	img
		width: 270px
		height: 36px
		vertical-align: middle
/* navigation */
.navigation
	overflow: hidden
	float: left
	max-width: calc(100% - 700px)
	height: 50px
	font-weight: 700
	font-size: 14px
	line-height: 18px
	> ul
		+pie-clearfix
		> li
			float: left
			border-left: 1px solid #DAE9E9
			&:last-child
				border-right: 1px solid #DAE9E9
			> a
				display: block
				padding: 0 15px
				line-height: 50px
				transition: all 0.2s
				span
					display: none
				.icon-triangle
					display: inline-block
					margin-left: 2px
					+arr(10,7, $grey-dark, b)
					transition: all 0.2s
			&.is-active, &:hover
				> a
					color: $orange
					transition: all 0.2s
					.icon-triangle
						border-color: $orange transparent transparent transparent
						transition: all 0.2s
.full-nav
	display: none
	position: relative
	padding-top: 15px
	padding-bottom: 15px
	background-color: #DAE9E9
	text-align: center
	&-btn
		display: inline-block
		padding: 5px 20px
		font-size: 12px
		color: #fff
		line-height: 14px
		background-color: $grey-dark
		border-radius: 5px
		text-decoration: none
		transition: all 0.2s
		span
			margin-left: 5px
		&:hover
			background-color: $orange
			transition: all 0.2s
.flexMenu-popup
	position: absolute
	top: 100%
	left: 0
	right: 0
	margin: 0 auto
	padding-top: 25px
	padding-bottom: 10px
	border-bottom: 1px solid #DAE9E9
	background-color: #fff
	+pie-clearfix
	.flexMenu-title
		float: left
		padding-left: 40px
		margin-right: 40px
		width: 310px
		font-size: 24px
		line-height: 29px
		text-transform: uppercase
		letter-spacing: 0.5em
	ul
		overflow: hidden
		margin-left: 310px
	li
		overflow: hidden
		float: left
		padding-right: 10px
		width: 20%
		margin-bottom: 15px
		white-space: nowrap
		text-overflow: ellipsis
		a
			color: #6E7E85
			transition: all 0.2s
			span
				margin-left: 5px
		&:hover
			a
				color: $orange
				transition: all 0.2s
.search-toggle
	float: left
	a
		display: block
		padding-left: 10px
		padding-right: 13px
		color: $grey-dark
		line-height: 50px
		border-right: 1px solid #DAE9E9
		transition: all 0.2s
		&:hover, &.is-active
			color: $orange
			transition: all 0.2s
		i
			position: relative
			top: -2px
			display: block
			font-size: 20px
.form-search
	display: none
	position: relative
	z-index: 1
	padding-top: 15px
	padding-bottom: 15px
	background-color: #DAE9E9
	&__title
		float: left
		padding-left: 40px
		margin-right: 40px
		width: 310px
		font-size: 24px
		line-height: 29px
		font-weight: 700
		text-transform: uppercase
		letter-spacing: 0.5em
	&__in
		overflow: hidden
		margin-left: 310px
	.field
		float: left
		width: 65%
		input
			padding-left: 15px
			padding-right: 15px
			width: 100%
			height: 40px
			font-family: $mon-r
			color: $grey-dark
			font-size: 14px
			line-height: 40px
			border-radius: 5px
			border: 1px solid #B6BEC2
			+placeholder-color(#D6D6DB)
		+ .field
			margin-left: 10px
			width: 80px
			button
				display: block
				width: 100%
				height: 40px
				font-family: $mon-r
				font-size: 15px
				color: #fff
				background-color: $grey-dark
				border-radius: 5px
				transition: all 0.2s
				&:hover
					background-color: $orange
					transition: all 0.2s
/* Login */
.header
	.login-btn
		float: right
		margin-top: 10px
		margin-left: 20px
		margin-right: 40px
.login-btn
	display: inline-block
	padding-left: 15px
	padding-right: 15px
	font-family: $mon-r
	font-size: 14px
	color: #fff
	font-weight: 700
	line-height: 30px
	border-radius: 5px
	background-color: $orange
	transition: all 0.2s
	&:hover
		background-color: $grey-dark
		transition: all 0.2s
/* socials */
.socials
	float: right
	margin-top: 15px
	font-size: 20px
	color: $grey-dark
	line-height: 20px
	+pie-clearfix
	li
		float: left
		margin-left: 7px
		a
			display: block
.header
	.socials
		li
			transition: all 0.3s
			&:hover
				color: $orange
				transition: all 0.3s
/* Popup */
.popup
	display: none
	position: fixed
	z-index: 1001
	left: 0
	right: 0
	top: 0
	bottom: 0
	font-family: $mon-r
	font-weight: 700
	background-color: rgba(45, 48, 71, .6)
	&__in
		position: absolute
		top: 50%
		left: 50%
		transform: translate(-50%,-50%)
		padding: 30px 85px 60px
		background-color: #fff
		border-radius: 5px
	&__close-btn
		position: absolute
		top: 10px
		right: 10px
		font-size: 24px
		color: #DAE9E9
		line-height: 24px
		transition: all 0.2s
		i
			display: block
		&:hover
			color: $grey-dark
			transition: all 0.2s
	&__title
		margin-bottom: 20px
		font-size: 28px
		color: $grey-dark
		line-height: 34px
		text-align: center
	a
		text-decoration: none
.popup-login
	&__list
		font-size: 15px
		color: #fff
		line-height: 19px
		+pie-clearfix
		li
			background-color: #49659F
			border-radius: 5px
			transition: all 0.2s
			&:hover
				background-color: darken(#49659F,20)
				transition: all 0.2s
			a
				position: relative
				display: block
				padding-top: 14px
				padding-bottom: 15px
				width: 290px
				padding-left: 75px
				i
					position: absolute
					left: 0
					top: 0
					height: 100%
					width: 60px
					font-size: 24px
					line-height: 24px
					text-align: center
					border-right: 1px solid rgba(255,255,255,.3)
					&:before
						position: absolute
						left: 50%
						top: 50%
						transform: translate(-50%,-50%)
						margin-left: 0
			+ li
				margin-top: 10px
				background-color: #55ACEE
				&:hover
					background-color: darken(#55ACEE,20)
				+ li
					background-color: #FE2230
					&:hover
						background-color: darken(#FE2230,20)
					i:before
						left: 55%
					+ li
						background-color: #FE5F00
						&:hover
							background-color: darken(#FE5F00,20)
						i:before
							left: 50%
.popup-sign
	.form-sign
		margin: 0 auto
		width: 290px
		+pie-clearfix
		.field
			input
				width: 100%
				padding-left: 10px
				padding-right: 10px
				padding-top: 11px
				padding-bottom: 13px
				font-family: $mon-r
				font-size: 17px
				color: $grey-dark
				line-height: 20px
				text-align: center
				border-radius: 5px
				border: 2px solid #DAE9E9
				+placeholder-color(#6E7E85)
			+ .field
				margin-top: 10px
			.submit-btn
				width: 100%
				padding-top: 14px
				padding-bottom: 15px
				font-family: $mon-r
				font-size: 15px
				color: #fff
				line-height: 19px
				text-transform: uppercase
				text-align: center
				background-color: $orange
				border-radius: 5px
				transition: all 0.2s
				&:hover
					background-color: $grey-dark
					transition: all 0.2s
/* grid section */
.grid-wrap
	position: relative
	overflow: hidden
	a
		text-decoration: none
		display: block
		height: 100%
.grid
	&__list
		+pie-clearfix
		&-el
			position: relative
			overflow: hidden
			float: left
			width: 20%
			height: 180px
			cursor: pointer
	&__img
		height: 100%
		img
			width: 100%
			min-height: 100%
			height: auto
			vertical-align: top
	&__tooltip
		position: absolute
		top: 100%
		left: 0
		width: 100%
		height: 100%
		font-weight: 700
		font-size: 18px
		color: #fff
		line-height: 25px
		background-color: rgba(45,48,71,.9)
		z-index: 1
		transition: all 0.6s
		> p
			padding: 20px
			height: 100%
	&__title
		display: none
		position: absolute
		left: 0
		top: 0
		width: 100%
		height: 100%
		background-color: rgba(45,48,71,.9)
		z-index: 99
		opacity: 0
		&-text
			margin: 0 auto
			position: relative
			width: 80%
			top: -40px
			margin-top: -60px
			text-align: center
			font-weight: 700
			font-size: 50px
			line-height: 1.3
			color: #fff
			span
				display: block
	&__btn-more
		position: absolute
		width: auto
		height: auto
		bottom: 20px
		right: 20px
		padding: 3px 5px 3px 10px
		font-size: 8px
		color: #fff
		line-height: 10px
		text-transform: uppercase
		background-color: $orange
		border-radius: 5px
@keyframes animatedBackground
	from
		background-position: 24px center
	to
		background-position: 0 center
.wave
	position: absolute
	left: 0
	bottom: -1px
	width: 100%
	height: 24px
	background-image: url("../img/icon.png")
	background-position: 0 center
	background-repeat: repeat-x
	z-index: 100
	animation: animatedBackground 3s linear infinite
/* hot-now section */
.hot-now
	margin-bottom: 40px
	background-color: rgba(254, 95, 0, 0.08)
	padding-top: 20px
	padding-bottom: 20px
	font-size: 16px
	line-height: 20px
	border-radius: 5px
	.row
		margin-left: -20px
		margin-right: -20px
	a
		text-decoration: none
	.section__title
		margin-bottom: 20px
		font-weight: 700
		font-size: 20px
		color: $orange
		line-height: 24px
		text-transform: uppercase
	.el
		display: inline-block
		padding-left: 20px
		padding-right: 20px
		margin-right: -4px
		width: 25%
		vertical-align: top
		> a
			display: block
			transition: all 0.2s
			&:hover
				color: $orange
				transition: all 0.2s
		&__img
			img
				width: 100%
				height: auto
				vertical-align: bottom
		&__text
			padding: 10px 0 20px
/* column-section */
.column-section
	font-size: 16px
	line-height: 20px
	+pie-clearfix
	a
		text-decoration: none
	.section__title
		margin-bottom: 20px
		font-weight: 700
		font-size: 20px
		line-height: 24px
		text-transform: uppercase
		&:after
			content: ''
			display: block
			margin-top: 10px
			height: 1px
			background-color: #DAE9E9
	> .row
		margin-left: -20px
		margin-right: -20px
	.el
		margin-bottom: 20px
		> a
			display: block
			transition: all 0.2s
			background-color: #DAE9E9
			&:hover
				color: $orange
				transition: all 0.2s
			img
				width: 100%
				height: auto
				vertical-align: bottom
		&__text
			padding: 10px 15px 20px
		&.el--small
			margin-bottom: 0
			> .row
				margin-left: -10px
				margin-right: -10px
			.el
				width: 50%
				display: inline-block
				padding-left: 10px
				padding-right: 10px
				vertical-align: top
				margin-right: -4px
				font-size: 15px
				line-height: 25px
	&.column-cat
		.el
			padding-left: 20px
			padding-right: 20px
			+r(1023)
				padding-left: 10px
				padding-right: 10px
.column-section
	> .row
		width: calc(100% + 40px)
		display: table
		table-layout: fixed
	.column
		display: table-cell
		padding-left: 20px
		padding-right: 20px
	.column-1
		width: 280px
	.column-2
		width: 500px
		font-size: 20px
		line-height: 25px
	.column-3
		width: 100%
		font-size: 25px
		line-height: 30px
		font-weight: 700
/* banner 100%x90px */
.mgid-hr
	margin-bottom: 40px
	height: 90px
	background-color: #F5F5F5 // for example
/* pagination */
.pagination
	+pie-clearfix
	a
		text-decoration: none
	&__button
		float: left
		padding-top: 8px
		padding-left: 21px
		padding-right: 21px
		width: 160px
		height: 60px
		font-weight: 700
		font-size: 32px
		color: #fff
		line-height: 39px
		background-color: $grey-dark
		border-radius: 5px
		transition: all 0.2s
		&:hover
			background-color: $orange
			transition: all 0.2s
	&__prev
		text-align: right
	&__next
		text-align: left
	&__list
		float: left
		margin: 17px 30px 0
		font-weight: 700
		font-size: 20px
		line-height: 24px
		li
			float: left
			&.is-active
				color: #fff
				border-radius: 5px
				background-color: $orange
			a
				display: block
				padding: 0 5px 2px
.icon-arrow_left
	float: left
	margin-top: 9px
	+sr(arrow_left)
.icon-arrow_right
	float: right
	margin-top: 9px
	+sr(arrow_right)
/* wrap-sm */
.wrap-sm
	padding-left: 40px
	padding-right: 40px
	+r(1023)
		padding-left: 20px
		padding-right: 20px
/* Subscribe */
.subscribe-wrap
	margin-top: 40px
	margin-bottom: 40px
	> .row
		padding-top: 40px
		border-top: 1px solid #DAE9E9
.subscribe
	float: left
	padding: 20px 38px 40px
	width: calc(100% - 320px)
	background-color: rgba(254,95,0,.08)
	border-radius: 5px
	&__title
		font-weight: 700
		font-size: 40px
		line-height: 50px
		span
			color: $orange
	+r(1023)
		width: 100%
.subscribe, .post
	.socials
		float: none
		margin-top: 22px
		margin-right: 0
		color: #fff
		li
			margin-left: 0
			margin-right: 10px
			background-color: #49659F
			border-radius: 5px
			transition: all 0.3s
			&:hover
				background-color: darken(#49659F,20)
				transition: all 0.3s
			a
				padding: 18px 17px 18px 15px
			+ li
				background-color: #55ACEE
				&:hover
					background-color: darken(#55ACEE,20)
				+ li
					background-color: #D1221E
					&:hover
						background-color: darken(#D1221E,20)
					+ li
						background-color: #FF2D31
						&:hover
							background-color: darken(#FF2D31,20)
						+ li
							background-color: #227598
							&:hover
								background-color: darken(#227598,20)
/* Mgidiply social vk/fb */
.mgidiply-soc
	float: right
	width: 300px
	height: 214px
	background-color: #F5F5F5 // for preview
	+r(1023)
		display: none
/* Footer */
.footer
	padding-top: 31px
	font-size: 15px
	line-height: 19px
	a
		text-decoration: none
	&__logo
		float: left
		+pie-clearfix
		p
			float: left
		&-link
			float: left
			margin-left: 20px
			width: 96px
			height: 19px
			img
				width: 100%
				height: auto
				vertical-align: -1px
	&__nav
		float: right
		ul
			+pie-clearfix
		li
			float: left
			margin-left: 20px
			a
				transition: all 0.2s
			&.is-active, &:hover
				a
					color: $orange
					transition: all 0.2s
#menu_toggle
	position: relative
	display: none
/*-------------------------------------------------*/
/*----------- Tablet Landscape Main Page ----------*/
/*-------------------------------------------------*/
.mobile.landscape
	.wrapper
		margin-left: auto
		margin-right: auto
		overflow: hidden
	#menu_toggle
		display: block
		float: left
		margin-top: 10px
		margin-left: 40px
		width: 60px
		height: 60px
		text-align: center
		background-color: $orange
		border-radius: 5px
		vertical-align: middle
		i
			margin-right: 5px
			display: inline-block
			color: #fff
			font-size: 38px
			line-height: 38px
		&.is-active
			i:before
				content: unquote("\"\\#{e807}\"")
	.navigation
		display: none
		position: absolute
		top: 100%
		left: 0
		right: 0
		margin: 0
		z-index: 1000
		font-weight: 700
		font-size: 42px
		line-height: 52px
		text-align: center
		background-color: #F5F5F5
		box-shadow: 0 10px 20px #000
		li
			display: block
			float: none
			padding-left: 25px
			padding-top: 13px
			padding-bottom: 14px
			border-bottom: 1px solid #DAE9E9
			+ li
				margin-left: 0
/*------------------------------------------------*/
/*----------- Tablet Portrait Main Page ----------*/
/*------------------------------------------------*/
.mobile.portrait
	#menu_toggle
		display: block
		float: left
		margin-top: 10px
		margin-left: 40px
		width: 60px
		height: 60px
		text-align: center
		background-color: $orange
		border-radius: 5px
		vertical-align: middle
		i
			margin-right: 5px
			display: inline-block
			color: #fff
			font-size: 38px
			line-height: 38px
		&.is-active
			i:before
				content: unquote("\"\\#{e807}\"")
	.navigation
		display: none
		position: absolute
		top: 100%
		left: 0
		right: 0
		margin: 0
		z-index: 1000
		background-color: #F5F5F5
		font-weight: 700
		font-size: 42px
		line-height: 52px
		text-align: center
		box-shadow: 0 10px 20px #000
		li
			display: block
			float: none
			padding-top: 13px
			padding-bottom: 14px
			border-bottom: 1px solid #DAE9E9
			+ li
				margin-left: 0
	.grid
		&__title
			&-text
				width: 100%
				margin-top: -80px
				font-size: 38px
				line-height: 50px
	.hot-now
		padding-bottom: 12px
		.el
			margin-bottom: 20px
			padding-left: 10px
			padding-right: 10px
			width: 50%
	.top-trend
		.row
			margin-left: -10px
			margin-right: -10px
		.el
			margin-bottom: 80px
			width: 100%
			&.el--small
				margin-bottom: 0
				.el
					margin-bottom: 40px
					width: 50%
/*------------------------------------------------*/
/*----------------- Mobile Main Page -------------*/
/*------------------------------------------------*/
.mobile.phone
	.grid
		&__list
			&-el
				height: auto
				&:nth-child(1)
					~ li
						display: block
		&__img
			position: relative
			z-index: -1
		&__tooltip
			top: 0
			background: linear-gradient(to bottom, rgba(45, 48, 71, 0), rgba(45, 48, 71, 1))
			p
				position: absolute
				height: auto
				bottom: 100px
				left: 20px
				right: 20px
				font-size: 22px
				line-height: 32px
				text-align: center
		&__btn-more
			position: absolute
			bottom: 70px
			left: 50%
			margin-left: -39px
			width: 78px
